@import "variables";
@import "mixins";

@include ipad-landscape {
	table.tt_timetable {
		th {
			font-size: 16px;
		}
	}
}

@include ipad-portrait {
	table.tt_timetable{

		thead th,
		.event .event_header{
			font-size: 14px;
		}

		td,
		.event .after_hour_text,
		.event .before_hour_text{
			font-size: 13px;
		}

		td{
			padding: 0 4px;
		}
	}
}

@include phone-portrait {
	.qode-ttevents-single {
		.tt_event_hours li {
			width: 100%;
			margin-right: 0;
			margin-bottom: 30px;
		}
	}
}